html {
  overflow-y: auto;
  overflow-x: hidden; 
}

body, #root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background: url('./assets/brand.png') no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  overflow: visible !important;
}

.player-wrapper {
  position: relative;
  padding-top: 56.25%; /* 720 / 1280 = 0.5625 */
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
}

